import { mapState } from 'vuex';

export default {
  computed: {
    ...mapState('global', {
      bottom: state => state.bottom,
    }),
  },
  watch: {
    bottom() {
      this.refresh();
    },
  },
  methods: {
    refresh() {
      this.$refs.scrollView.refresh();
    },
  },
  activated() {
    // keep-alivebottomdom
    this.refresh();
  },
};
